export default {
	data() {
		return {
			hour: '',
			minu: '',
			sec: '',
			titleindex:0,
			titlebox:['拼团中','拼团成功','拼团失败'],
			open:false,
		}
	},
	mounted: function() {
		let date = new Date().getTime();
		let time = new Date(date)
		let hour = time.getHours();
		let minu = time.getMinutes();
		let sec = time.getSeconds();
		let millisecond = time.getMilliseconds(); //毫秒
		this.timer = setInterval(() => {
			console.log(millisecond)
			millisecond = millisecond - 1000;
			if (millisecond <= 0) {
				millisecond = 1000;
				sec = sec - 1;
			}
			if (sec <= -1) {
				sec = 59;
				minu = minu - 1;
			}
	
			if (minu <= -1) {
				minu = 59;
				hour = hour - 1;
			}
			if (sec <= 9) {
				sec = "0" + sec;
			}
			this.hour = hour
			this.minu = minu
			this.sec = sec
		}, 1000);
	},
	methods: {
		tapindex(index){
			this.titleindex = index
		},
		open_box:function(){
			this.open = !this.open
		},
		close:function(){
			this.open = !this.open
		}
	}
}
